import React, { useLayoutEffect } from "react";
import Marquee from "react-fast-marquee";
import "./Scrollslide.css";
import gsap from "gsap";

const Scrollslide = () => {
  useLayoutEffect(() => {
    let ctx = gsap.context(() => {
      const scroller = document.querySelector(".scrolls");
      gsap.from(scroller, {
        duration: 1.5,
        opacity: 0,
        y: 50,
        ease: "expo.out(1)",
        delay: "3.4"
      })

    });

    return () => ctx.revert();
  })
  return (
    <div className="scrolls">
      <Marquee speed={140}>
        <div className="scrollslide">
          <span>Kickstart your Masterpiece!</span>
          <span>✹</span>
          <span>Kickstart your Masterpiece!</span>
          <span>✹</span>
          <span>Kickstart your Masterpiece!</span>
          <span>✹</span>
          <span>Kickstart your Masterpiece!</span>
          <span>✹</span>
        </div>
      </Marquee>
    </div>
  );
};

export default Scrollslide;
