import React, { useEffect, useLayoutEffect } from "react";
import Header from "../Header/Header";
import "./About.css";
import img from "./light2.webp";
import Footer from "../Footer/Footer";
import gsap from "gsap";
import { Link } from "react-router-dom";

const About = () => {
  useEffect(() => {
    const timer = setTimeout(() => {
      document.querySelector(".path").classList.add("animate");
    }, 3000);

    return () => clearTimeout(timer);
  }, []);

  useLayoutEffect(() => {
    const fadeinElements = document.querySelectorAll(".fadein");

    if (fadeinElements.length > 0) {
      let ctx = gsap.context(() => {
        gsap.from(fadeinElements, {
          duration: .9,
          y: "50px",
          opacity: 0,
          stagger: 0.3,
          delay: 1
        });
      });
      return () => ctx.revert();
    }
  }, []);

  return (
    <div className="about-page">
      <div className="about">
        <img className="bg-img" src={img} alt="bg-img" />
        <Header />
        <div className="about-sec">
          <h1 className="about-head fadein">About Us</h1>
          <p className="fadein">
            At
            <span className="highlight">
              Speculation
              <svg
                className="round"
                fill="none"
                viewBox="0 0 155 79"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  className="path"
                  d="m80.8824 68.1606c-41.5541 4.949-76.17197 1.1843-78.61979-19.1566-2.447821-20.3409 29.25619-40.84387 70.80579-45.79287 41.5536-4.949 77.2216 7.52487 79.6646 27.86577 2.444 20.3408-29.256 40.8439-70.8054 45.7974"
                  stroke="#D0FF71"
                  strokeLinecap="round"
                  strokeMiterlimit="10"
                  strokeWidth="4"
                />
              </svg>
            </span>
            Infotech, we are dedicated to transforming ideas into reality
            through innovative web development and comprehensive educational
            services. Our team is passionate about helping individuals and
            businesses succeed in the digital world.
          </p>
          <h2 className="fadein">Contact Us</h2>
          <p className="fadein">
            Join us to advance your skills and career. Reach out today!
          </p>
        </div>
        <Link to="/contact">
        <button className="cnt-button fadein">Contact Us</button>
        </Link>
      </div>
      <Footer />
    </div>
  );
};

export default About;
