import React, { useEffect, useRef, useState } from "react";
import Card from "../Card/Card";
import "./Programming.css";
import C from './images/c-programming.png';
import cplusplus from './images/c++.jpg';
import java from './images/java.png';
import python from './images/python.png';
import php from './images/php.webp';
import javascript from './images/javascript.webp';
import nodejs from './images/nodejs.png';
import reactjs from './images/ReactJS.png';
import xml from './images/xml.png';
import Wavetxt from "../Wavetxt/Wavetxt";


import { gsap } from 'gsap';
import SplitType from 'split-type';
import { ScrollTrigger } from 'gsap/ScrollTrigger'; 


const Programming = () => {
  const [showAll, setShowAll] = useState(false);

  const courses = [
    { title: 'C', description: 'After learning C, it will be much easier to learn other programming languages like Java, Python, etc. Opportunity to work on open-source projects, including some of the largest such as the Linux kernel and Python interpreter.', image:  C},
    { title: 'Python', description: 'Python is known for its readability and simplicity, making it an ideal first language. It’s used in web development, data science, artificial intelligence, scientific computing, and more.', image: python },
    { title: 'C++', description: 'C++ provides a strong foundation in object-oriented programming and system-level programming. It enhances problem-solving skills and is widely used in game development, real-time systems, and large-scale applications.', image: cplusplus },
    { title: 'Java', description: 'Learn about Operating Systems, Programming Fundamentals, Object-Oriented Concepts, EIM Fundamentals, Core Java, Industry Practices, JUnit, J2EE Architecture, Servlets, JSP, and more.', image:  java},
    { title: 'PHP', description: 'PHP is widely used for web development and server-side scripting. Learn about dynamic website creation, interacting with databases, and creating robust, scalable web applications.', image:  php},
    { title: 'Javascript', description: 'JavaScript is essential for front-end web development. It allows for creating interactive and dynamic websites. Learn the fundamentals, including DOM manipulation, event handling, and asynchronous programming.', image:  javascript},
    { title: 'Node.js', description: 'Node.js enables server-side scripting with JavaScript. It’s perfect for building fast, scalable network applications. Learn about event-driven programming, non-blocking I/O, and working with databases and web sockets.', image:  nodejs},
    { title: 'ReactJS', description: 'ReactJS is a powerful library for building user interfaces. Learn about component-based architecture, state management, hooks, and building interactive, high-performance web applications.', image:  reactjs},
    { title: 'XML', description: 'XML is used for data representation and transfer. Learn about defining custom tags, structuring data, and using XML with web services and APIs for data interchange.', image:  xml}
  ];

  const toggleShowAll = () => {
    setShowAll(!showAll);
  };

  const textRef = useRef(null);

    useEffect(() => {
        gsap.registerPlugin(ScrollTrigger);

        const text = textRef.current;
        const split = new SplitType(text, { types: 'chars, words' });
        const chars = split.chars;

        gsap.from(chars, {
            y: 20,
            opacity: 0,
            stagger: 0.05,
            ease: 'back.out(2)',
            scrollTrigger: {
                trigger: text,
                start: 'top 80%', 
                end: 'bottom 20%',
                toggleActions: 'play none none none',
            },
        });
        return () => {
        };
    }, []);


  return (
    <div className="programming course-head">
      <h1 ref={textRef}>Our Courses</h1>
      <Wavetxt txt = "Programming Courses"/>
      <div className="card-container">
        {courses.slice(0, 3).map((course, index) => (
          <Card key={index} course={course} />
        ))}

        {showAll &&
          courses
            .slice(3)
            .map((course, index) => <Card key={index} course={course} />)}
      </div>
      <button onClick={toggleShowAll} className="toggle-button">
        {showAll ? "Show Less" : "Show More"}
      </button>
    </div>
  );
};

export default Programming;
