import React from "react";
import phone from "./phone.svg";
import mail from "./mail.svg";
import location from "./location.svg";
import "./Footer.css";

const Footer = () => {
  return (
    <div className="footer">
      <div className="footer-cont">
        <div className="comp-logo">LOGO</div>
        <div className="navi">
          <p className="footer-head">Quick Navigation</p>
          <ul className="navi-links">
            <li>
              <a href="/">Home</a>
            </li>
            <li>
              <a href="/">About</a>
            </li>
            <li>
              <a href="/">Courses</a>
            </li>
          </ul>
        </div>
        <div className="connections">
          <p className="footer-head">Connections</p>
          <div className="phone">
            <img src={phone} alt="phone" />
            <p>6369687598</p>
          </div>
          <div className="mail">
            <img src={mail} alt="mail" />
            <a href="mailto:info@speculation.co.in">info@speculation.co.in</a>
          </div>
          <div className="location">
            <img src={location} alt="location" />
            <p>
              Speculation Info Tech, Kannan Ravi towers, Anna salai, <span>Trichy - 620002</span>
            </p>
          </div>
        </div>
      </div>
      <p className="copyright">Copyright © 2024 All Rights Reserved.</p>
    </div>
  );
};

export default Footer;
