import React, { useLayoutEffect, useEffect } from "react";
import "./Header.css";
import gsap from "gsap";
import { Link } from "react-router-dom";

const Header = () => {
  useLayoutEffect(() => {
    const ctx = gsap.context(() => {
      const header = document.querySelector(".header");
      const logo = document.querySelector(".logo");
      const nav = document.querySelectorAll(".nav-links li");
      const pop = document.querySelectorAll(".pop");

      const tl = gsap.timeline();

      tl.from(header, {
        width: "10px",
        duration: 1.5,
        ease: "power3.out(2)",
      });

      tl.from(
        logo,
        {
          duration: 0.5,
          scale: 0,
          stagger: 0.2,
          ease: "power.out(2)",
        },
        "-=1"
      );

      tl.from(
        nav,
        {
          duration: 0.5,
          scale: 0,
          stagger: 0.2,
          ease: "power.out(2)",
        },
        "-=.8"
      );

      tl.from(
        pop,
        {
          duration: 0.5,
          scale: 0,
          stagger: 0.2,
          ease: "power.out(2)",
        },
        "-=.3"
      );
    });

    return () => ctx.revert();
  }, []);

  useEffect(() => {
    const hamburger = document.querySelector(".hamburger");
    const navLinks = document.querySelector(".nav-links");
    const links = document.querySelectorAll(".nav-links li");

    const toggleNav = () => {
      navLinks.classList.toggle("open");
      links.forEach((link) => {
        link.classList.toggle("fade");
      });
      hamburger.classList.toggle("toggle");
    };

    hamburger.addEventListener("click", toggleNav);

    return () => {
      hamburger.removeEventListener("click", toggleNav);
    };
  }, []);

  return (
    <div className="header">
      <svg
        id="Layer_1"
        data-name="Layer 1"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 216.34 180.58"
        className="logo"
      >
        <path
          className="path1"
          d="M1133.26,510.71a87.1,87.1,0,0,0-74.53-39H933.52a.39.39,0,0,0-.34.58l21.59,38.27a.4.4,0,0,0,.34.2h100.57c29.63,2.79,53,25.86,56,53.95,3.29,30.86-18.83,60.58-51.19,66.42v.11h-53.39l38.36-67.45-25.2.5-38.2,67h0L970.86,652l89.66.2c6.89-.31,41.71-2.58,66.83-31.3a89.84,89.84,0,0,0,21.46-47.85C1152.79,540.65,1137.45,516.8,1133.26,510.71Z"
          transform="translate(-933.13 -471.64)"
        />
        <path
          className="path2"
          d="M1080.29,532.23c-9.1-6.93-18.42-11.05-27.83-11l-90.61.64,27.93,33.05,107.9-.19A56.89,56.89,0,0,0,1080.29,532.23Z"
          transform="translate(-933.13 -471.64)"
        />
        <path
          className="path3"
          d="M1075.91,585.58a21.84,21.84,0,0,0,21.77-21.84h-43.75l-11.9,22Z"
          transform="translate(-933.13 -471.64)"
        />
      </svg>
      <div className="hamburger">
        <div className="line1"></div>
        <div className="line2"></div>
        <div className="line3"></div>
      </div>
      <ul className="nav-links">
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/about">About</Link>
        </li>
        <li>
          <a href="/">Courses</a>
        </li>
      </ul>
      <Link to="/contact">
        <button className="contact-btn pop" type="button">
          Contact Us
        </button>
      </Link>
    </div>
  );
};

export default Header;
