import React from "react";
import "./Contact.css";
import img from "./light2.webp";
import Header from "../Header/Header";
import phone from "../Footer/phone.svg";
import mail from "../Footer/mail.svg";
import location from "../Footer/location.svg";
import Footer from "../Footer/Footer";

const Contact = () => {
  

  return (
    <div className="contact">
        <div className="contact-page">
      <img className="bg-img" src={img} alt="bg-img" />
      <Header />
      <h1 className="cont-head">Contact Us</h1>
      <div className="contact-div">
        <div className="cont-left">
          <form className="contact-form">
            <p>Name:</p>
            <input
              type="text"
              name="name"
            />
            <p>Email:</p>
            <input
              type="email"
              name="email"
            />
            <p>message:</p>
            <textarea
              name="message"
            />
            <button className="cnt-btn" type="submit">
              Send
            </button>
          </form>
        </div>
        <div className="cont-right">
          <div className="phone">
            <img src={phone} alt="phone" />
            <p>6369687598</p>
          </div>
          <div className="mail">
            <img src={mail} alt="mail" />
            <a href="mailto:info@speculation.co.in">info@speculation.co.in</a>
          </div>
          <div className="location">
            <img src={location} alt="location" />
            <p>
              Speculation Info Tech, Kannan Ravi towers, Anna salai,{" "}
              <span>Trichy - 620002</span>
            </p>
          </div>
        </div>
      </div>
    </div>
      <Footer />
    </div>
  );
};

export default Contact;
