import React from "react";
import './Card.css';

const Card = ({course}) => {
  
  return (
    <div className="card">
      <div className="card-image">
        <img className="card-image" src={course.image} alt="" />
      </div>
      <p className="card-title">{course.title}</p>
      <p className="card-body">{course.description}</p>
    </div>
  );
};

export default Card;
